<script>
import Talent from "./Talent.vue";
import Utility from "./Utility.vue";
import Power from "./Power.vue";
import { Delete } from "@element-plus/icons-vue";
import { saveAs } from 'file-saver';
export default {
  components:{
    Talent,
    Utility,
    Delete,
    Power
  },
  name: 'Chatacter',
  props: {
    msg: String
  },
  data:function(){return{
      character:{
        name:"Character's Name",
        level:1,
        key:0,
        precision:0,
        agility:0,
        body:0,
        mind:0,
        speed:6,
        classA:"",
        classB:"",
        armor:0,
        surges:4,
        powers:[],
        utilities:[],
        talents:[],
        skills:[]
      },
      toEdit:null,
      editTalent:false,
      editPower:false,
      editUtility:false
  }},
  computed:{
    attack(){ return +this.character.precision + +this.proficiency},
    initiative(){return +this.character.agility},
    proficiency(){ return this.character.level<12?
      1+Math.ceil(this.character.level/4):this.character.level<19?
      5:this.character.level<25?
      6:this.character.level<29?7:
      8},
    armorType(){switch(this.character.armor){
      case "1": return this.character.agility;
      case "2": return this.character.body;
      case "3": return this.character.mind;
      default: return 0;
    }},
    AC(){return +8 + +this.armorType + +this.proficiency},
    tier(){return Math.ceil(this.character.level/10)},
    //maxHP(){return (50+(this.body*10))*this.tier},
    maxHP(){return 100*this.tier},
    keystat(){switch(this.character.key){
      case 1: return this.character.agility;
      case 2: return this.character.body;
      case 3: return this.character.mind;
      default: return 0;
    }},
    keyAbi(){ return ["","agility","body","mind"][this.character.key]},
    armorAbi(){ return ["","agility","body","mind"][this.character.armor]},
    fortitude(){ return +8 + +this.character.body + +this.proficiency },
    reflex(){ return +8 + +this.character.agility + +this.proficiency },
    will(){ return +8 + +this.character.mind + +this.proficiency },
    savingThrow(){ return +10 + +this.keystat },
    abiInvested(){
        let base = Math.floor((this.character.level)/10)*4;
        let cur = (this.character.level) % 10
        let add =(cur<2?1:cur<4?2:cur<8?3:4);
        return + 9 + +base  + add
      - (+this.character.precision + +this.character.agility + +this.character.body + +this.character.mind)
    },
  powerSlots(){
    return 3 + Math.floor((this.character.level-1)/10)
  },
  lesserUtil(){
        let base = Math.floor((this.character.level)/10)*4;
        let cur = (this.character.level) % 10
        let add = 2 + (cur<8?Math.floor(cur/2):4);
    return base + add
  },
  greaterUtil(){
    return 1+Math.floor(this.character.level/10)
  }
  },
  methods:{
    getRandomInt(min, max) {
      return Math.floor(Math.random() * (max - min)) + min;
    },makeID(table){
      let ret = this.getRandomInt(1,Number.MAX_SAFE_INTEGER)
      while(table.some(p=>p.id==ret)){
        ret = this.getRandomInt(1,Number.MAX_SAFE_INTEGER)
      }
      return ret;
    },
    getBonus(i){
    let abi;
      switch(parseInt(i)){
        case 1: abi= this.character.agility; 
        break;
        case 2: abi= this.character.body; 
        break;
        case 3: abi= this.character.mind; 
        break;
        case 4: abi= this.character.precision; 
        break;
        default: abi= 0; 
        break;
      }
      let ret = +abi + +this.proficiency;
      return ret
    },
    setKey(i){
      this.character.key=(this.character.key==i)?0:i;
    },
    addSkill(){
      this.character.skills.push({id:this.makeID(this.character.skills),name:"Skill Name",ability:0})
    },
    addTalent(){
      this.character.talents.push({id:this.makeID(this.character.talents),name:"Talent",tier:1,description:"Description..."})
    },
    addUtility(){
      this.character.utilities.push({
        id:this.makeID(this.character.utilities),
        name:"Utility",
        type:"Minor Utility",
        tier:1,
        description:"Description..."})
    },
    addPower(){
      this.character.powers.push({
      id:this.makeID(this.character.powers),
      name:"Power",
      tier:1,
      description:"Description...",
      usage:"At-Will",
      shape:"Shape",
      keywords:["Keyword 1","Keyword 2"],
      action:"",
      target:"Target",
      attack:"Attack",
      hit:"Hit",
      miss:"Miss",
      aftereffects:"",
      effects:"",
      special:""
    })
    },
    discardEdit(type){
      switch(type){
        case 1:
          this.editTalent=false;
        break;
        case 2:
          this.editPower=false;
        break;
        case 3:
          this.editUtility=false;
        break;
      }
      this.toEdit=null
    },
    raiseEdit(i,type){
      switch(type){
        case 1:
          this.editTalent=true;
          this.toEdit=this.character.talents.find(t=>t.id==i)
        break;
        case 2:
          this.editPower=true;
          this.toEdit=this.character.powers.find(p=>p.id==i)
        break;
        case 3:
          this.editUtility=true;
          this.toEdit=this.character.utilities.find(u=>u.id==i)
        break;
      }
      this.toEdit={...this.toEdit}
    },
    saveEdit(i,type){
      switch(type){
        case 1:
          this.character.talents.splice(this.character.talents.findIndex(t=>t.id==i),1,this.toEdit)
          this.editTalent=false;
        break;
        case 2:
          this.character.powers.splice(this.character.powers.findIndex(p=>p.id==i),1,this.toEdit)
          this.editPower=false;
        break;
        case 3:
          this.character.utilities.splice(this.character.utilities.findIndex(u=>u.id==i),1,this.toEdit)
          this.editUtility=false;
        break;
      }
    },handleDelete(i,type){
      switch(type){
        case 1:
          this.character.talents=this.character.talents.filter(t=>t.id!=i)
        break;
        case 2:
          this.character.powers=this.character.powers.filter(t=>t.id!=i)
        break;
        case 3:
          this.character.utilities=this.character.utilities.filter(t=>t.id!=i)
        break;
        case 4:
          this.character.skills=this.character.skills.filter(t=>t.id!=i)
        break;
      }
    }
    ,
    log(x){ console.log(x)},
    saveCharacter(){
      saveAs(new Blob([JSON.stringify(this.character)],{type: "application/json"}),this.character.name+".fpf")
    },
    async loadCharacter(){
      let fileHandle;
      fileHandle = await window.showOpenFilePicker({ multiple: false })
      const file = await fileHandle[0].getFile();
      const contents = await file.text()
      this.character = JSON.parse(contents);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
